/* Global Reset and Background */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: black;
}

html, body {
    width: 100%;
    height: 100%;
    background-color: black;
    overflow-x: hidden;
}

.App {
    min-height: 100vh;
    width: 100vw;
    background-color: black;
    overflow-x: hidden;
}

/* Loading Screen */
.loading-screen {
    background-color: black;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Press Start 2P', cursive;
    color: #9c6bff;
}

    .loading-screen p {
        font-size: 1rem;
        margin-bottom: 1rem;
    }

.progress-bar {
    width: 150px;
    height: 6px;
    background-color: #222;
    border-radius: 4px;
    overflow: hidden;
}

.bar-fill {
    height: 100%;
    width: 80%;
    background-color: #9c6bff;
    transition: width 0.5s ease-in-out;
}

/* Header Navigation */
.header {
    text-align: center;
    padding: 1rem;
    background-color: transparent;
}

.nav-links a {
    color: #9c6bff;
    margin: 0 1rem;
    text-decoration: none;
    font-weight: bold;
    font-family: 'Press Start 2P', cursive;
}

    .nav-links a:hover {
        text-decoration: underline;
    }

/* Hero Section */
.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 1rem 0;
    background-color: black;
}

.hero-image {
    max-width: 250px;
    height: auto;
    border-radius: 10px;
}

.hero-text {
    text-align: center;
    margin-top: 1rem;
    color: #fff;
    font-family: 'Press Start 2P', cursive;
}

    .hero-text h1 {
        color: #9c6bff;
        font-size: 1rem;
        margin-bottom: 0.5rem;
    }

    .hero-text p {
        font-size: 0.75rem;
    }

/* Section Styling */
.section, .about {
    padding: 2rem 1rem;
    background-color: black;
    color: #00ffcc;
    font-family: 'Press Start 2P', cursive;
    text-align: center;
}

    .section h2,
    .about h2 {
        color: #00ffcc;
        margin-bottom: 1rem;
    }

    .section p,
    .about p {
        max-width: 700px;
        margin: auto;
        font-size: 0.8rem;
        line-height: 1.6;
    }

/* Projects Section */
.projects {
    padding: 2rem 0;
    background-color: black;
    text-align: center;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding: 1rem;
}

.project-card {
    background-color: #1a1a1a;
    padding: 1rem;
    margin: 1rem auto;
    border-radius: 12px;
    max-width: 320px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.05);
    color: #9c6bff;
}

    .project-card img {
        width: 100%;
        border-radius: 8px;
        margin-bottom: 0.5rem;
    }

    .project-card h3 {
        font-size: 0.95rem;
    }

    .project-card p {
        font-size: 0.7rem;
        color: #dcdcdc;
    }

    .project-card a {
        display: inline-block;
        margin-top: 0.5rem;
        color: #9c6bff;
        font-size: 0.7rem;
        text-decoration: none;
    }

        .project-card a:hover {
            text-decoration: underline;
        }

/* Contact Section */
.contact {
    text-align: center;
    padding: 2rem;
    background-color: black;
    font-family: 'Press Start 2P', cursive;
}

    .contact h2 {
        color: #ffffff;
        margin-bottom: 1rem;
    }

    .contact p {
        color: #f5f5f5;
        font-size: 0.8rem;
        margin: 0.5rem 0;
    }

    .contact a {
        color: #ffea00;
        text-decoration: none;
        display: inline-block;
        margin-top: 0.5rem;
    }

        .contact a:hover {
            text-decoration: underline;
        }

.contact-social {
    margin-top: 1rem;
}

    .contact-social a {
        font-size: 1.5rem;
        margin: 0 0.75rem;
        color: #9c6bff;
        transition: transform 0.3s ease;
    }

        .contact-social a:hover {
            transform: scale(1.2);
        }

/* Footer */
.footer {
    background-color: black;
    color: #ccc;
    text-align: center;
    padding: 1rem 0;
    font-size: 0.75rem;
    font-family: 'Press Start 2P', cursive;
}
